import React from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'

import { removeOne } from '../redux/cart/cart.actions'

import addComma from '../utils/helpers/addComma'
import slugifyProduct from '../utils/helpers/slugifyProduct'

const CartItem = ({ _id, name, quantity, imageURL, price, removeOne }) => {
  const productLink = slugifyProduct(name)

  const handleRemoveFromCart = () => {
    removeOne(_id)
  }

  return (
    <div className='cart-item'>
      <figure style={{ backgroundImage: `url(${imageURL})` }} />
      <Link to={productLink} className='name'>
        {name}
      </Link>
      <p className='info'>
        quantity: {quantity} &nbsp;&times;&nbsp; ${price}
      </p>

      <p className='total-price'>
        ${addComma(parseFloat(quantity * price).toFixed(2))}
      </p>
      <i className='close-icon' onClick={handleRemoveFromCart}>
        {quantity > 1 ? '' : ''}
      </i>
    </div>
  )
}

export default connect(null, { removeOne })(CartItem)
